.FixedButton {
  width: 100%;
  height: 52px;
  overflow: hidden;
  display: none; }
  .FixedButton .Anchor {
    display: block;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    line-height: 52px;
    background-color: #7f00a6;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
    text-align: center;
    text-decoration: none;
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
    opacity: .01;
    -webkit-transition: opacity .2s, -webkit-transform .3s ease-out;
    transition: opacity .2s, -webkit-transform .3s ease-out;
    transition: transform .3s ease-out, opacity .2s;
    transition: transform .3s ease-out, opacity .2s, -webkit-transform .3s ease-out;
    z-index: 3; }
    .FixedButton .Anchor.Show {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
      opacity: 1; }
  @media (max-width: 640px) {
    .FixedButton {
      display: block; } }



