.SessionPair {
  position: relative;
  padding-bottom: 60px; }
  .SessionPair:last-child {
    padding-bottom: 0; }
  .SessionPair:not(:first-child) {
    margin-top: 16px; }
  .SessionPair .SessionItem {
    width: calc(50% - 95px);
    font-size: 14px; }
    .SessionPair .SessionItem:nth-child(1) {
      float: left; }
      .SessionPair .SessionItem:nth-child(1) > div {
        text-align: right; }
    .SessionPair .SessionItem:nth-child(2) {
      float: right; }
      .SessionPair .SessionItem:nth-child(2) > div {
