.SponsorLogo {
  display: inline-block;
  margin: 24px 15px 0 0;
  vertical-align: middle; }
  .SponsorLogo.DIAMOND {
    max-width: 220px; }
  .SponsorLogo.PLATINUM {
    max-width: 158px; }
  .SponsorLogo.GOLD {
    max-width: 120px; }
  .SponsorLogo.ETC {
    max-width: 90px; }
  .SponsorLogo .Anchor {
    display: block;
    width: 100%;
    height: 100%; }
  .SponsorLogo .Image {
    width: 100%; }
  @media (max-width: 640px) {
    .SponsorLogo {
      max-width: 120px;
      margin-right: 20px; }
      .SponsorLogo.DIAMOND {
        max-width: 150px; }
      .SponsorLogo.PLATINUM {
        max-width: 120px; }
      .SponsorLogo.GOLD {
        max-width: 90px; }
      .SponsorLogo.ETC {
        max-width: 60px; } }



